/* Behåll en enkel layout och centrera innehåll */
.container {
    max-width: 800px;
    margin: 0 auto;
    padding: 1rem;
    font-family: Arial, sans-serif;
    color: #333;
  }
  
  /* Styling för rubriker */
  h2 {
    margin-top: 2rem;
    color: #444;
  }
  
  /* Styling för filuppladdningssektioner */
  input[type="file"],
  input[type="text"] {
    display: block;
    margin: 0.5rem 0;
    padding: 0.5rem;
    font-size: 1rem;
    width: 100%;
    max-width: 300px;
  }
  
  /* Styling för knappar */
  button {
    padding: 0.5rem 1rem;
    font-size: 1rem;
    margin: 0.5rem 0;
    background-color: #007bff;
    border: none;
    border-radius: 4px;
    color: white;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #0056b3;
  }
  
  /* Styling för meddelanden */
  p {
    margin: 0.5rem 0;
  }
  
  p.error {
    color: red;
  }
  
  /* Styling för den nedladdade bilden */
  img {
    max-width: 100%;
    height: auto;
    display: block;
    margin-top: 1rem;
    border: 1px solid #ccc;
    padding: 0.5rem;
    border-radius: 4px;
  }

  .pickwheel-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 20px 0;
  }
  
  .pickwheel {
    width: 200px;
    height: 40px;
    font-size: 16px;
    text-align: center;
    border-radius: 20px;
    border: 1px solid #ccc;
    appearance: none;
    outline: none;
    background: #f7f7f7;
    padding: 5px 10px;
    cursor: pointer;
  }
  
  .pickwheel option {
    text-align: center;
  }